import { defineStore } from "pinia";
import axios from "axios";
import { User, Users } from "@/hooks/useUser";


/**
 * useUserStoreuser
 */

const useUserStore = defineStore('user_store', {
    state: () => ({
        users: [] as Users,
    }),
    getters: {
        getUsers: (s) => s.users,
    },
    actions: {

        /**
         * 
         */
        async request(): Promise<boolean> {
            try {
                this.users = await (await axios.get('/api/user/findAll')).data
            } catch {
                this.users = [
                    {
                        u_id: '1',
                        u_name: ' ',
                        u_pwd: 'wow ',
                        isAdmin: false
                    },
                    {
                        u_id: '2',
                        u_name: 'name 1 ',
                        u_pwd: '00 ',
                        isAdmin: false
                    },
                    {
                        u_id: '3',
                        u_name: ' 1 ',
                        u_pwd: 'l ',
                        isAdmin: true
                    }
                ]
                return false
            }
            return true
        },

        /**
         * ID
         */
        searchByAnyField(keyword: string): Users {
            return this.users.filter(e => e.u_id.toString() == keyword || e.u_name.includes(keyword) || e.u_pwd.includes(keyword) || ((keyword === '' || keyword === '') && e.isAdmin) || ((keyword === '' || keyword === '') && !e.isAdmin))
        },

        /**
         * 
         * @see Promise<boolean>thenawait
         */
        async refresh(): Promise<boolean> {
            try {
                var data = await this.request()
            } catch {
                return false
            }
            return data
        },

        /**
         * 
         */
        async remove(e: User) {
            await axios.delete('/api/user/deleteOneById', {
                params: {
                    u_id: e.u_id
                }
            }).then(() => {
                this.users.splice(this.users.indexOf(e), 1)    
            })
        },
        
        edit(target: User, e: User) {
            console.log(target, e);
            axios.post('/api/user/alterUser', {
                u_id: e.u_id,
                u_name: e.u_name,
                u_pwd: e.u_pwd
            }).then(() => {
                console.log('success');
            })
        }
    }
})

export { useUserStore }
export type { User }